import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DetailProdukPage } from '../detail-produk/detail-produk';
import { DetailUkmPage } from '../detail-ukm/detail-ukm';

/**
 * Generated class for the JenisUkmPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-jenis-ukm',
  templateUrl: 'jenis-ukm.html',
})
export class JenisUkmPage {

  item: any;
  data: any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {

    this.item = this.navParams.data;

    console.log('**navParams:', this.navParams);
  }

  
  back(){
    this.navCtrl.pop();
  } 

  itemTapped(){
    this.navCtrl.push(DetailUkmPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad JenisUkmPage');
  }

}



