import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ModalController  } from 'ionic-angular';
import { HostProvider } from '../../providers/host/host';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ProfilPage } from '../profil/profil';
import { JenisProdukPage } from '../jenis-produk/jenis-produk';
import { JenisUkmPage } from '../jenis-ukm/jenis-ukm';

/**
 * Generated class for the HomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  home: string = "home-produk";
  isAndroid: boolean = false;
  activeButton: any;
  data: any;

  public isSearchBarOpened = false;

  test = [
    { id: 1,
      jenis_produk: 'TEST PRODUK 1',
      foto: 'assets/icon/item 1.svg'
    },
    { id: 2,
      jenis_produk: 'TEST PRODUK 2',
      foto: 'assets/icon/Location.svg'
    },
    { id: 3,
      jenis_produk: 'TEST PRODUK 3',
      foto: 'assets/icon/item 1.svg'
    },
  ];

  test_ukm = [
    { id: 1,
      jenis_ukm: 'TEST UKM 1',
      foto: 'assets/imgs/Logo Daerah/Bekasi.jpg'
    },
    { id: 2,
      jenis_ukm: 'TEST UKM 2',
      foto: 'assets/imgs/Logo Daerah/Kabupaten Bandung.png'
    },
    { id: 3,
      jenis_ukm: 'TEST UKM 3',
      foto: 'assets/imgs/Logo Daerah/Kabupaten Garut.jpg'
    },
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams, public platform: Platform, public host: HostProvider, public http: HttpClient, public modalCtrl: ModalController) {
    this.isAndroid = platform.is('android');
  }

  setActiveButton(button){
    this.activeButton = button;
  }

  itemTapped($event, item){
    this.navCtrl.push(JenisProdukPage, item);
  }

  itemTappedUkm($event, item){
    this.navCtrl.push(JenisUkmPage, item);
  }

  onSearch(event) {
    console.log(event.target.value);
  }

  halamanProfile(){
    this.navCtrl.push(ProfilPage);
  }


  public openModal(){
    var data = { message : 'success' };
    var modalPage = this.modalCtrl.create('FilterPage',data);
     modalPage.present();
  }   

  getKategoriProduk(){

      this.http.get(this.host.baseUrl + "/data/kategori-produk")
      .subscribe(
        data => {
          this.data = data;
          console.log(this.data);
        }, (err) => {
          console.log("error : " + err);
          console.log(err);
        });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HomePage');

    this.getKategoriProduk(); 
  }

}



